import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

const Sidebar = () => {
  const [open, setOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const location = useLocation();

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
      if (window.innerWidth > 768) {
        setOpen(true); // open sidebar on desktop by default
      }
    };

    handleResize(); // initialize
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const sidebarStyle = {
    width: open ? (isMobile ? '100%' : '220px') : '48px',
    background: open ? 'rgba(248, 250, 255, 0.95)' : '#f8faff',
    backdropFilter: 'blur(8px)',
    WebkitBackdropFilter: 'blur(8px)',
    padding: open ? '1.2rem' : '1.2rem 0.5rem',
    transition: 'all 0.2s ease',
    overflow: 'hidden',
    height: '100vh',
    boxShadow: open ? '2px 0 12px rgba(0,0,0,0.05)' : 'none',
    position: 'fixed',
    left: 0,
    top: 0,
    zIndex: 100,
  };

  const linkStyle = (path) => ({
    display: 'block',
    padding: '0.8rem 1rem',
    marginBottom: '0.5rem',
    borderRadius: '8px',
    color: location.pathname === path ? '#1976d2' : '#333',
    backgroundColor:
      location.pathname === path
        ? 'rgba(227, 242, 253, 0.9)'
        : 'transparent',
    textDecoration: 'none',
    fontWeight: location.pathname === path ? '600' : '500',
    fontSize: '1.1rem',
    transition: 'all 0.2s ease',
    backdropFilter: 'blur(4px)',
    WebkitBackdropFilter: 'blur(4px)',
  });

  const buttonStyle = {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    fontSize: '1.5rem',
    marginBottom: open ? '1.5rem' : '0',
    marginLeft: open ? '0' : '0.2rem',
    color: '#1976d2',
    padding: '0.5rem',
    borderRadius: '6px',
    transition: 'all 0.2s ease',
    position: isMobile && !open ? 'fixed' : 'relative',
    top: isMobile && !open ? 10 : undefined,
    left: isMobile && !open ? 10 : undefined,
    zIndex: isMobile && !open ? 2000 : undefined,
    backgroundColor: isMobile && !open ? '#f8faff' : 'transparent',
  };

  return (
    <>
      <div className="sidebar" style={sidebarStyle}>
        <button
          onClick={() => setOpen(!open)}
          style={buttonStyle}
          aria-label={open ? 'Close sidebar' : 'Open sidebar'}
        >
          {open ? '🇯🇵 OMG JLPT' : '☰'}
        </button>

        {open && (
          <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
            {['n1', 'n2', 'n3', 'n4', 'n5'].map((level) => (
              <li key={level}>
                <Link
                  to={`/old_questions/${level}`}
                  style={linkStyle(`/old_questions/${level}`)}
                  onClick={() => isMobile && setOpen(false)}
                >
                  {level.toUpperCase()} Level
                </Link>
              </li>
            ))}
            {/* <li>
              <Link
                to="/old_questions/ocr"
                style={linkStyle('/old_questions/ocr')}
                onClick={() => isMobile && setOpen(false)}
              >
                OCR Reader
              </Link>
            </li>
            <li>
              <Link
                to="/old_questions/romaji-to-hiragana"
                style={linkStyle('/old_questions/romaji-to-hiragana')}
                onClick={() => isMobile && setOpen(false)}
              >
                Romaji To Hiragana
              </Link>
            </li> */}
          </ul>
        )}
      </div>

      <style>{`
        .sidebar {
          border-right: 1px solid rgba(230, 230, 230, 0.7);
        }

        .sidebar button:hover {
          background-color: rgba(240, 247, 255, 0.9) !important;
        }

        .sidebar a:hover {
          background-color: rgba(240, 247, 255, 0.9) !important;
          transform: translateX(4px);
        }
      `}</style>
    </>
  );
};

export default Sidebar;
