import { useState, useEffect } from 'react';
import N5DataJa from '../../../assets/n5/n5Data.ja.json';
import N5DataEn from '../../../assets/n5/n5Data.en.json';
// import N5DataMm from '../../../assets/n5/N5Data.mm.json';

import MojiGoiQ1 from '../../shared/MojiGoiQ1';
import MojiGoiQ2 from '../../shared/MojiGoiQ2';
import MojiGoiQ3 from '../../shared/MojiGoiQ3';
import MojiGoiQ4 from '../../shared/MojiGoiQ4';
import MojiGoiQ5 from '../../shared/MojiGoiQ5';

import GrammarQ1 from '../../shared/GrammarQ1';
import GrammarQ2 from '../../shared/GrammarQ2';
import GrammarQ3 from '../../shared/GrammarQ3';
import ReadingQ1 from '../../shared/Reading1';

const LANGUAGES = [
  { key: 'ja', label: '日本語 (🇯🇵)' },
  { key: 'en', label: 'English (🇺🇸)' },
  // { key: 'mm', label: 'မြန်မာ (🇲🇲) ' }
];

const SECTION_LABELS = {
  en: {
    kanji_vocab: 'Kanji /Vocab',
    grammar_reading: 'Grammar /Reading',
    listening: 'Listening'
  },
  ja: {
    kanji_vocab: '文字・語彙',
    grammar_reading: '文法・読解',
    listening: '聴解'
  },
  // mm: {
  //   kanji_vocab: 'ခန်းဂျီး/အဘိဓာန်',
  //   grammar_reading: 'သဒ္ဒါ /စာဖတ်ခြင်း',
  //   listening: 'နားထောင်ခြင်း'
  // }
};

const SECTIONS = [
  { key: 'kanji_vocab' },
  { key: 'grammar_reading' },
  { key: 'listening' }
];

const dataMap = {
  ja: N5DataJa,
  en: N5DataEn,
  // mm: N5DataMm
};

// Helper to check if a question is answered (single value only)
function isAnswered(val) {
  return val !== undefined && val !== null && val !== '';
}

export default function N5() {
  const [lang, setLang] = useState('ja');
  const [section, setSection] = useState('kanji_vocab');
  const [year, setYear] = useState(dataMap[lang][0]?.year);
  const [selectedAnswers, setSelectedAnswers] = useState({});
  const [showResult, setShowResult] = useState(false);
  const [showDialog, setShowDialog] = useState(false);
  const [showScrollTop, setShowScrollTop] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setShowScrollTop(window.scrollY > 200);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const years = dataMap[lang].map(entry => entry.year);
  const yearData = dataMap[lang].find(entry => entry.year === year);
  const title = yearData?.title || '';

  // Kanji/Vocab questions (flat array)
  const kanjiVocabQuestions = [
    ...(yearData?.sections?.moji_goi_q1 || []),
    ...(yearData?.sections?.moji_goi_q2 || []),
    ...(yearData?.sections?.moji_goi_q3 || []),
    ...(yearData?.sections?.moji_goi_q4 || []),
    ...(yearData?.sections?.moji_goi_q5 || []),
  ];

  // Reading questions (flatten if nested, include q1 and q2)
  const grammarReadingQuestions = [
    ...(Array.isArray(yearData?.sections?.bunpoo_q1)
      ? yearData.sections.bunpoo_q1.flat()
      : yearData?.sections?.bunpoo_q1 || []),
    ...(Array.isArray(yearData?.sections?.bunpoo_q2)
      ? yearData.sections.bunpoo_q2.flat()
      : yearData?.sections?.bunpoo_q2 || []),
      ...(Array.isArray(yearData?.sections?.bunpoo_q3)
      ? yearData.sections.bunpoo_q3.flat()
      : yearData?.sections?.bunpoo_q3 || []),
      ...(Array.isArray(yearData?.sections?.dokai_q1)
      ? yearData.sections.dokai_q1.flat()
      : yearData?.sections?.dokai_q1 || [])
  ];

  // Section-specific questions for confirm/reset button
  const questions =
    section === 'kanji_vocab'
      ? kanjiVocabQuestions
      : section === 'grammar_reading'
        ? grammarReadingQuestions
        : yearData?.sections?.[section] || [];

  // Section-specific totals for dialog
  const totalQuestions =
    section === 'kanji_vocab'
      ? kanjiVocabQuestions.length
      : section === 'grammar_reading'
        ? grammarReadingQuestions.length
        : 0;

  const answeredCount =
    section === 'kanji_vocab'
      ? kanjiVocabQuestions.filter(q => isAnswered(selectedAnswers[q.id])).length
      : section === 'grammar_reading'
        ? grammarReadingQuestions.filter(q => isAnswered(selectedAnswers[q.id])).length
        : 0;

  const correctCount =
    section === 'kanji_vocab'
      ? kanjiVocabQuestions.filter(q => selectedAnswers[q.id] === q.correct).length
      : section === 'grammar_reading'
        ? grammarReadingQuestions.filter(q => selectedAnswers[q.id] === q.correct).length
        : 0;

  const notAnsweredCount = totalQuestions - answeredCount;

  const handleAnswerChange = (questionId, answerIdx) => {
    setSelectedAnswers(prev => ({
      ...prev,
      [questionId]: answerIdx
    }));
  };

  const handleConfirm = () => {
    setShowResult(true);
    setShowDialog(true);
  };

  const handleReset = () => {
    setShowResult(false);
    setSelectedAnswers({});
    setShowDialog(false);
  };

  const handleCloseDialog = () => {
    setShowDialog(false);
  };

  return (
    <div>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end', marginBottom: '1rem', gap: '2rem' }}>
        {/* Language Switch */}
        {LANGUAGES.map(l => (
          <label key={l.key} style={{ marginRight: '1rem' }}>
            <input
              type="radio"
              name="lang"
              value={l.key}
              checked={lang === l.key}
              onChange={() => {
                setLang(l.key);
                setYear(dataMap[l.key][0]?.year);
                setSelectedAnswers({});
                setShowResult(false);
                setShowDialog(false);
              }}
            />
            {l.label}
          </label>
        ))}
        {/* Year Selector */}
        <select
          value={year}
          onChange={e => {
            setYear(e.target.value);
            setSelectedAnswers({});
            setShowResult(false);
            setShowDialog(false);
          }}
          style={{
            fontSize: '1.2rem',
            padding: '0.4rem 1rem',
            borderRadius: '6px',
            border: '1px solid #1976d2',
            color: '#1976d2',
            background: '#f8faff'
          }}
        >
          {years.map(y => (
            <option key={y} value={y}>{y}</option>
          ))}
        </select>
      </div>
      {/* Title */}
      {/* {title && (
        <div style={{ fontWeight: 'bold', fontSize: '1.2rem', marginBottom: '1rem' }}>
          {title}
        </div>
      )} */}
      {/* Section Selector */}
      <div style={{ marginBottom: '1rem', borderBottom: '2px solid #ccc', display: 'flex', gap: '0.5rem' }}>
        {SECTIONS.map(s => (
          <button
            key={s.key}
            onClick={() => {
              setSection(s.key);
              setSelectedAnswers({});
              setShowResult(false);
              setShowDialog(false);
            }}
            style={{
              background: section === s.key ? '#fff' : '#f5f5f5',
              color: section === s.key ? '#1976d2' : '#333',
              border: '1px solid #ccc',
              borderBottom: section === s.key ? '2px solid #fff' : '2px solid #ccc',
              borderRadius: '8px 8px 0 0',
              padding: '0.5rem 1.5rem',
              fontWeight: section === s.key ? 'bold' : 'normal',
              outline: 'none',
              cursor: 'pointer',
              marginBottom: '-2px'
            }}
          >
            {SECTION_LABELS[lang][s.key]}
          </button>
        ))}
      </div>

      {/* N5 Question => Kanji/Vocab */}
      {section === 'kanji_vocab' && (
        <>
          <MojiGoiQ1
            questions={yearData?.sections?.moji_goi_q1 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
          <MojiGoiQ2
            questions={yearData?.sections?.moji_goi_q2 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
          <MojiGoiQ3
            questions={yearData?.sections?.moji_goi_q3 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
          <MojiGoiQ4
            questions={yearData?.sections?.moji_goi_q4 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
          <MojiGoiQ5
            questions={yearData?.sections?.moji_goi_q5 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
        </>
      )}

      {section === 'grammar_reading' && (
        <>
          <GrammarQ1
            questions={yearData?.sections?.bunpoo_q1 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />

          <GrammarQ2
            questions={yearData?.sections?.bunpoo_q2 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />

          <GrammarQ3
            questions={yearData?.sections?.bunpoo_q3 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />

          <ReadingQ1
            questions={yearData?.sections?.dokai_q1 || []}
            selectedAnswers={selectedAnswers}
            showResult={showResult}
            handleAnswerChange={handleAnswerChange}
          />
          

          {/* Add more reading components as needed */}
        </>
      )}

      {section === 'listening' && (
        <>
          {/* Render your listening components here */}
        </>
      )}

      {/* Confirm Button */}
      {
        questions.some(q => q.answers) && (
          <div
            style={{
              position: 'fixed',
              bottom: 24,
              left: '220px', // match sidebar width
              right: 0,
              display: 'flex',
              justifyContent: 'center',
              zIndex: 200
            }}
          >
            <button
              onClick={handleConfirm}
              style={{
                background: '#1976d2',
                color: '#fff',
                border: 'none',
                padding: '0.7rem 2rem',
                borderRadius: '4px',
                fontSize: '1.2rem',
                cursor: 'pointer',
                boxShadow: '0 2px 8px rgba(25,118,210,0.07)'
              }}
            >
              Confirm
            </button>
            {showResult && (
              <button
                onClick={handleReset}
                style={{
                  marginLeft: '1rem',
                  background: '#eee',
                  color: '#000',
                  border: 'none',
                  padding: '0.7rem 2rem',
                  borderRadius: '4px',
                  fontSize: '1.2rem',
                  cursor: 'pointer'
                }}
              >
                Reset
              </button>
            )}
          </div>
        )
      }

      {/* Result Dialog */}
      {showDialog && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100vw',
            height: '100vh',
            background: 'rgba(0,0,0,0.3)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 1000
          }}
        >
          <div
            style={{
              background: '#fff',
              padding: '2rem',
              borderRadius: '8px',
              minWidth: '320px',
              boxShadow: '0 2px 16px rgba(0,0,0,0.2)'
            }}
          >
            <h2>Result Summary</h2>
            <div>Total questions: {totalQuestions}</div>
            <div>Answered: {answeredCount}</div>
            <div>Correct: {correctCount}</div>
            <div>Not answered: {notAnsweredCount}</div>
            <button
              onClick={handleCloseDialog}
              style={{
                marginTop: '1rem',
                background: '#1976d2',
                color: '#fff',
                border: 'none',
                padding: '0.5rem 1.5rem',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              Close
            </button>
          </div>
        </div>
      )}

      {/* Back to Top Button */}
      {showScrollTop && (
        <button
          onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
          style={{
            position: 'fixed',
            bottom: 90,
            right: 40,
            zIndex: 999,
            background: '#1976d2',
            color: '#fff',
            border: 'none',
            borderRadius: '50%',
            width: 48,
            height: 48,
            fontSize: '2rem',
            boxShadow: '0 2px 8px rgba(25,118,210,0.15)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}
          aria-label="Back to top"
          title="Back to top"
        >
          ↑
        </button>
      )}
    </div>
  );
}