import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import N5 from './jlpt/n5/N5';
import N4 from './jlpt/n4/N4';
import N3 from './jlpt/n3/N3';
import N2 from './jlpt/n2/N2';
import N1 from './jlpt/n1/N1';

// Single source of truth for JLPT levels
const levelContents = {
  n5: <N5 />,
  n4: <N4 />,
  n3: <N3 />,
  n2: <N2 />,
  n1: <N1 />
};

export default function JlptHome({ defaultLevel = 'n3' }) {
  const [selected, setSelected] = useState(defaultLevel);
  const location = useLocation();

  useEffect(() => {
    // Extract level from pathname (n1, n2, n3, n4, or n5)
    const level = location.pathname.match(/n[1-5]/)?.[0];
    if (level && levelContents[level]) {
      setSelected(level);
    }
  }, [location]);

  return (
    <div>
      <div>
        {levelContents[selected] || levelContents[defaultLevel]}
      </div>
    </div>
  );
}