import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Sidebar from './components/Sidebar';
import JlptHome from './pages/JlptHome';
// import OCRReader from './pages/shared/OCRReader';
// import RomajiToHiragana from './pages/shared/RomajiToHiragana';
import { initGA, logPageView } from './utils/analytics';

export default function App() {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);

  useEffect(() => {
    // Initialize Google Analytics
    initGA();
    logPageView(window.location.pathname);
    
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Router>
      <div style={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row' }}>
        <Sidebar isMobile={isMobile} />
        <div
          style={{
            marginLeft: isMobile ? 0 : 220,
            padding: '1rem',
            width: '100%',
            boxSizing: 'border-box',
            minHeight: '100vh',
            transition: 'margin-left 0.3s ease',
          }}
        >
          <Switch>
            <Route path="/old_questions/n1">
              <JlptHome defaultLevel="n1" />
            </Route>
            <Route path="/old_questions/n2">
              <JlptHome defaultLevel="n2" />
            </Route>
            <Route path="/old_questions/n3">
              <JlptHome defaultLevel="n3" />
            </Route>
            <Route path="/old_questions/n4">
              <JlptHome defaultLevel="n4" />
            </Route>
            <Route path="/old_questions/n5">
              <JlptHome defaultLevel="n5" />
            </Route>
            {/* <Route path="/old_questions/ocr">
              <OCRReader />
            </Route>
            <Route path="/old_questions/romaji-to-hiragana">
              <RomajiToHiragana />
            </Route> */}
            <Route path="/">
              <JlptHome defaultLevel="n3" />
            </Route>
          </Switch>
        </div>
      </div>
    </Router>
  );
}
